
import cdIcon from "./../../icon/src/icon.vue";
import selectBox from "./selectBox.vue";
import { defineComponent, ref, watch } from "vue";
const __default__ = defineComponent({
  name: "cd-cascader",
  components: {
    cdIcon,
    selectBox,
  },
  emits: ["update:modelValue", "change"],
  props: {
    placeholder: {
      type: String,
      default: "请选择",
    },
    height: {
      type: Number,
      default: 32,
    },
    width: {
      type: Number,
      default: 240,
    },
    option: {
      type: Array,
      required: true,
    },
    showAllLevels: {
      type: Boolean,
      default: true,
    },
    name: {
      type: String,
    },
  },
  setup(props, context) {
    // 设置大小
    let heightData = ref();
    let widthData = ref();
    setSize();
    function setSize() {
      if (props.height >= 24) {
        heightData.value = props.height;
      } else {
        heightData.value = 24;
      }
      if (props.width >= 200) {
        widthData.value = props.width;
      } else {
        widthData.value = 200;
      }
    }
    let info = ref();
    let isFocus = ref(false);
    let isShow = ref(false);
    //设置isFocus的值
    function setIsFocus() {
      if (isFocus.value) {
        isFocus.value = false;
        setTimeout(() => {
          isShow.value = false;
        }, 180);
      } else {
        isFocus.value = true;
        isShow.value = true;
      }
    }
    window.addEventListener("mousedown", () => {
      if (isFocus.value == true) {
        isFocus.value = false;
        setTimeout(() => {
          isShow.value = false;
        }, 180);
      }
    });
    // 获得多级选项的数据
    function getCascaderData(data: any) {
      if (props.showAllLevels == true) {
        info.value.value = data;
        setInputValue(data);
      } else {
        info.value.value = data.split("/")[data.split("/").length - 1];
        setInputValue(data.split("/")[data.split("/").length - 1]);
      }
    }
    function setInputValue(data: any) {
      context.emit("change", data);
      context.emit("update:modelValue", data);
    }
    return {
      isFocus,
      setIsFocus,
      getCascaderData,
      info,
      heightData,
      widthData,
      isShow,
    };
  },
});

import { useCssVars as _useCssVars } from 'vue'
const __injectCSSVars__ = () => {
_useCssVars(_ctx => ({
  "a6923860": (_ctx.widthData + "px"),
  "da76066e": (_ctx.heightData + "px"),
  "f11beaf4": (_ctx.heightData/2 + "px"),
  "7db9393d": (_ctx.heightData + 10 + "px")
}))}
const __setup__ = __default__.setup
__default__.setup = __setup__
  ? (props, ctx) => { __injectCSSVars__();return __setup__(props, ctx) }
  : __injectCSSVars__

export default __default__