
import cdprogress from "./progress.vue";
import cdIcon from "./../../icon/src/icon.vue";
import { defineComponent, ref, watch, onMounted, watchEffect } from "vue";
const __default__ = defineComponent({
  components: {
    cdIcon,
    cdprogress,
  },
  name: "cd-upload",
  emits: [],
  props: {
    type: {
      type: String,
      default: "list",
    },
    height: {
      type: Number,
      default: 32,
    },
    width: {
      type: Number,
      default: 240,
    },
    action: {
      type: String,
      required: true,
    },
    method: {
      type: String,
      default: "POST",
    },
    accept: {
      type: String,
    },
    showFileList: {
      type: Boolean,
      default: true,
