
import cdIcon from "./../../icon/src/icon.vue";
import { defineComponent, inject, onMounted, ref, watch } from "vue";
const __default__ = defineComponent({
  name: "cd-dialog",
  emits: ["update:modelValue", "open", "close"],
  components: {
    cdIcon,
  },
  props: {
    modelValue: {
      type: Boolean,
      required: true,
    },
    width: {
      type: Number,
      default: 50,
    },
    top: {
      type: Number,
      default: 15,
    },
    title: {
      type: String,
    },
    text: {
      type: String,
    },
    center: {
      type: Boolean,
      defalut: false,
    },
  },
  setup(props, context) {
    let isTextSlot = ref<boolean>(true);
    let textSlot = ref();
    onMounted(() => {
      if (textSlot.value.clientHeight > 0) {
        isTextSlot.value = false;
      }
    });
    watch(
      () => {
        return props.modelValue;
      },
      (newval: boolean, oldval) => {
        if (newval) {
          context.emit("open");
        } else {
          context.emit("close");
        }
      }
    );
    // 关闭键的hover
    let isHover = ref<boolean>(false);
    function onMouseenter() {
      isHover.value = true;
    }
    function onMouseleave() {
      isHover.value = false;
    }
    function onClose() {
      context.emit("update:modelValue", false);
    }
    return {
      textSlot,
      isTextSlot,
      onMouseenter,
      onMouseleave,
      isHover,
      onClose,
    };
  },
});

