
// 只能单个使用，要多个配合使用时要使用el-checkbox-group组件
import { computed, ref, watch } from "vue";
import { defineComponent } from "vue";
const __default__ = defineComponent({
  name: "cd-checkbox",
  emits: ["update:modelValue", "onChange"],
  props: {
    modelValue: {
      type: Boolean,
      default: false,
    },
    size: {
      type: Number,
      default: 50,
    },
    label: {
      type: String,
    },
    disabled: {
      type: Boolean,
      default: false,
    },
    border: {
      type: Boolean,
      default: false,
    },
    name: {
      type: String,
    },
    value: {
      type: String,
    },
  },
  setup(props, context) {
    //   设置大小
    let sizeData = ref();
    if (props.size <= 50) {
      sizeData.value = 50;
    } else {
      sizeData.value = props.size;
    }

    // 选中还是没有选中
    let checkData = ref(false);
    checkData.value = props.modelValue;
    // 设置图标的颜色
    let selectIconColor = ref();
    function setSelectIconColor() {
      if (props.disabled) {
        selectIconColor.value = "#cdd2d8";
      } else {
        selectIconColor.value = "white";
      }
    }
    // 设置图标的背景颜色
    let selectIconBackground = ref();
    function setSelectIconBackground() {
      if (props.disabled) {
        selectIconBackground.value = "#edf2fc";
      } else {
        selectIconBackground.value = "#409eff";
      }
    }
    // 设置字体内容的颜色
    let contextColor = ref("");
    function setContextColor() {
      if (checkData.value == true) {
        contextColor.value = "#409eff";
      } else {
        contextColor.value = "black";
      }
      if (props.disabled) {
        contextColor.value = "#C0C4CC";
      }
    }
    // 设置选中还是不选中(点击字的时候)
    function setCheckData() {
      if (props.disabled == false) {
        checkData.value = !checkData.value;
        setContextColor();
      }
    }
    function onChange(data: any) {
      context.emit("onChange", data);
    }
    // 防止双击选中
    function onSelectstart(event: any) {
      event.preventDefault();
    }
    function setModelValue(data: any) {
      context.emit("update:modelValue", data);
    }

    watch(
      () => {
        return props.disabled;
      },
      (newval, oldval) => {
        setSelectIconColor();
        setSelectIconBackground();
        setContextColor();
      },
      { immediate: true }
    );
    //点击框的时候
    watch(
      checkData,
      (newval, oldval) => {
        setModelValue(newval);
        setContextColor();
        onChange(checkData.value);
      },
      { immediate: true }
    );
    return {
      checkData,
      setCheckData,
      onSelectstart,
      contextColor,
      sizeData,
      selectIconBackground,
      selectIconColor,
    };
  },
});

import { useCssVars as _useCssVars } from 'vue'
const __injectCSSVars__ = () => {
_useCssVars(_ctx => ({
  "6f1fd30b": (_ctx.sizeData / 2+ "px"),
  "12a09318": (_ctx.disabled==true?'not-allowed':'pointer'),
  "4691ffcc": (_ctx.sizeData/3 + "px"),
  "5aed1834": (_ctx.sizeData/2.1/2-_ctx.sizeData/3/2 + "px"),
  "7583a175": (_ctx.selectIconBackground),
  "365112bc": (_ctx.selectIconColor),
  "4d864fc6": (_ctx.contextColor)
}))}
const __setup__ = __default__.setup
__default__.setup = __setup__
  ? (props, ctx) => { __injectCSSVars__();return __setup__(props, ctx) }
  : __injectCSSVars__

export default __default__