      if (props.trigger == "hover") {
      } else {
        triggerHoverInd.value = presentInd.value;
      }
    }
    return {
      presentImg,
      isLeftMove,
      onMouseover,
      onMouseout,
      isArrowShow,
      lastPage,
      isRightMove,
      nextPage,
      triggerMouseover,
      triggerMouseout,
      triggerHoverInd,
      triggerClick,
    };
  },
});

