
import cdIcon from "./../../icon/src/icon.vue";
import { defineComponent, ref, watch, onMounted, watchEffect } from "vue";
const __default__ = defineComponent({
  components: {
    cdIcon,
  },
  name: "cd-switch",
  emits: ["update:modelValue", "change"],
  props: {
    modelValue: {
      type: Boolean,
      default: false,
    },
    height: {
      type: Number,
      default: 32,
    },
    width: {
      type: Number,
      default: 40,
    },
    activeColor: {
      type: String,
      default: "#409EFF",
    },
    inactiveColor: {
      type: String,
      default: "#C0CCDA",
    },
    activeText: {
      type: String,
    },
    inactiText: {
      type: String,
    },
    activeIcon: {
      type: String,
    },
    inactiIcon: {
      type: String,
    },
    disabled: {
      type: Boolean,
      default: false,
    },
  },
  setup(props, context) {
    //   设置大小
    let heightData = ref();
    let widthData = ref();
    setSize();
    function setSize() {
      if (props.height >= 20) {
        heightData.value = props.height;
      } else {
        heightData.value = 20;
      }
      if (props.width >= 40) {
        widthData.value = props.width;
      } else {
        widthData.value = 40;
      }
    }
    let isTrue = ref();
    watch(
      () => {
        return props.modelValue;
      },
      (newval, oldval) => {
        isTrue.value = newval;
      },
      { immediate: true }
    );
    function setIsTrue() {
      if (props.disabled) {
        return;
      }
      isMove.value = true;
      isTrue.value = !isTrue.value;
      setModelValue();
      executeChange();
    }
    function executeChange() {
      context.emit("change", isTrue.value);
    }
    function setModelValue() {
      context.emit("update:modelValue", isTrue.value);
    }
    let isMove = ref(false);
    return {
      heightData,
      widthData,
      isMove,
      isTrue,
      setIsTrue,
    };
  },
});

