
import cdIcon from "./../../icon/src/icon.vue";
import { defineComponent, ref, watch, onMounted } from "vue";
const __default__ = defineComponent({
  components: {
    cdIcon,
  },
  name: "cd-rate",
  emits: ["change", "update:modelValue"],
  props: {
    modelValue: {
      type: Number,
      default: 0,
    },
    max: {
      type: Number,
      default: 5,
    },
    height: {
      type: Number,
      default: 32,
    },
    disabled: {
      type: Boolean,
      default: false,
    },
    lowThreshold: {
      type: Number,
      default: 2,
    },
    highThreshold: {
      type: Number,
      default: 4,
    },
    colors: {
      type: Array,
      default: ["#F7BA2A", "#F7BA2A", "#F7BA2A"],
    },
    voidColor: {
      type: String,
      default: "#C6D1DE",
    },
    showScore: {
      type: Boolean,
      default: false,
    },
    textColor: {
      type: String,
      default: "#1F2D3D",
    },
    iconName: {
      type: String,
      default: "star",
    },
  },
  setup(props, context) {
    let rate: any = ref([]);
    const setRate = (el: any) => {
      rate.value.push(el);
    };
    let hoverScore = ref();
    let confirmScore = ref();
    // 初始化确定的分数
    initializeConfirmScore();
    function initializeConfirmScore() {
      confirmScore.value = Math.floor(props.modelValue);
      if (confirmScore.value <= 0) {
        confirmScore.value = 0;
      }
      if (confirmScore.value >= props.max) {
        confirmScore.value = props.max;
      }
    }

    // 鼠标经过的icon的时候
    let hoverDate = ref();
    let hoverColor = ref();
    function onMouseover(ind: number) {
      if (props.disabled) {
        return;
      }
      hoverDate.value = ind + 1;
      hoverScore.value = hoverDate.value;
      if (hoverDate.value <= props.lowThreshold) {
        hoverColor.value = props.colors[0];
      } else if (
        hoverDate.value > props.lowThreshold &&
        hoverDate.value < props.highThreshold
      ) {
        hoverColor.value = props.colors[1];
      } else if (hoverDate.value >= props.highThreshold) {
        hoverColor.value = props.colors[2];
      }
    }
    // 设置大小
    let heightDate = ref();
    setSize();
    function setSize() {
      if (props.height >= 24) {
        heightDate.value = props.height;
      } else {
        heightDate.value = 24;
      }
    }
    // 是否触发hover
    let isHover = ref(false);
    let confirmData = ref(0);
    confirmData.value = props.modelValue;
    function setIsHover(parameter: number) {
      if (parameter == 1 && props.disabled == false) {
        isHover.value = true;
      } else if (parameter == 0) {
        if (confirmData.value <= props.lowThreshold) {
          hoverColor.value = props.colors[0];
        } else if (
          confirmData.value > props.lowThreshold &&
          confirmData.value < props.highThreshold
        ) {
          hoverColor.value = props.colors[1];
        } else if (confirmData.value >= props.highThreshold) {
          hoverColor.value = props.colors[2];
        }
        isHover.value = false;
      }
    }
    // 设置确定的得分
    function getConfirmData(ind: number) {
      if (props.disabled) {
        return;
      }
      if (isHover.value) {
        confirmData.value = ind + 1;
        confirmScore.value = ind + 1;
        context.emit("change", confirmScore.value);
      } else {
        return;
      }
    }

    onMounted(() => {
      setIsHover(0);
    });

    return {
      hoverDate,
      onMouseover,
      hoverColor,
      heightDate,
      setIsHover,
      confirmData,
      isHover,
      getConfirmData,
      hoverScore,
      confirmScore,

      rate,
      setRate,
    };
  },
});

import { useCssVars as _useCssVars } from 'vue'
const __injectCSSVars__ = () => {
_useCssVars(_ctx => ({
  "37f1f7dc": (_ctx.heightDate/1.3*_ctx.max+'px'),
  "7c83e5c7": (_ctx.heightDate + "px"),
  "cc5bf7ca": (_ctx.heightDate/1.8 + "px"),
  "61fc7765": (_ctx.textColor),
  "64fbd8e3": (_ctx.heightDate/3 + "px")
}))}
const __setup__ = __default__.setup
__default__.setup = __setup__
  ? (props, ctx) => { __injectCSSVars__();return __setup__(props, ctx) }
  : __injectCSSVars__

export default __default__