
import cdIcon from "./../../icon/src/icon.vue";
import { defineComponent, onMounted, ref, watch } from "vue";
const __default__ = defineComponent({
  name: "cd-empty",
  components: {
    cdIcon,
  },
  props: {
    img: {
      type: String,
      default:
        "https://acmphoto.oss-cn-beijing.aliyuncs.com/CW_%E6%97%A0%E7%89%A9%E5%93%81.png",
    },
    imageSize: {
      type: Number,
      default: 200,
    },
    description: {
      type: String,
      default: "No Data",
    },
  },
  setup() {
    // 描述
    let isDescriptionDom = ref(true);
    let descriptionDom = ref();
    let descriptionHeight = ref();
    let isImgDom = ref(true);
    let imgDom = ref();
    let imgHeight = ref();
    let isDefaultDom = ref(true);
    let defaultDom = ref();
    let defaultHeight = ref();
    onMounted(() => {
      descriptionHeight.value = descriptionDom.value.clientHeight;
      if (descriptionHeight.value > 0) {
        isDescriptionDom.value = true;
      } else {
        isDescriptionDom.value = false;
      }
      imgHeight.value = imgDom.value.clientHeight;
      if (imgHeight.value > 0) {
        isImgDom.value = true;
      } else {
        isImgDom.value = false;
      }
      defaultHeight.value = defaultDom.value.clientHeight;
      if (defaultHeight.value > 0) {
        isDefaultDom.value = true;
      } else {
        isDefaultDom.value = false;
      }
    });
    return {
      descriptionHeight,
      descriptionDom,
      isDescriptionDom,
      isImgDom,
      imgDom,
      imgHeight,
      isDefaultDom,
      defaultDom,
      defaultHeight,
    };
  },
});

import { useCssVars as _useCssVars } from 'vue'
const __injectCSSVars__ = () => {
_useCssVars(_ctx => ({
  "68fd83dd": (_ctx.imageSize + "px")
}))}
const __setup__ = __default__.setup
__default__.setup = __setup__
  ? (props, ctx) => { __injectCSSVars__();return __setup__(props, ctx) }
  : __injectCSSVars__

export default __default__