
import cdIcon from "./../../icon/src/icon.vue";
import { defineComponent, onMounted, ref, watch, watchEffect } from "vue";
const __default__ = defineComponent({
  name: "cd-table",
  components: {
    cdIcon,
  },
  props: {
    data: {
      type: Array,
      required: true,
    },
    fieldName: {
      type: Array,
      required: true,
    },
    listName: {
      type: Array,
      required: true,
    },
    sort: {
      type: Array,
    },
    stripe: {
      type: Boolean,
      default: false,
    },
    border: {
      type: Boolean,
      default: false,
    },
    fixedHeader: {
      type: Boolean,
      default: false,
    },
    rows: {
      type: Number,
      default: 5,
    },
    width: {
      type: Number,
      default: 32,
    },
  },
  setup(props, context) {
    // 设置大小
    let widthData = ref<number>();
    setSize();
    function setSize() {
      if (props.width >= 24) {
        widthData.value = props.width;
      } else {
        widthData.value = 24;
      }
    }
    //
    let options = ref();
    options.value = props.data;
    let isUp = ref<boolean[]>([]);
    let isDown = ref<boolean[]>([]);
    props.listName.forEach((val, ind) => {
      isUp.value[ind] = false;
      isDown.value[ind] = false;
    });
    function setSort(ind: number, data: number) {
      if (data == 0) {
        isUp.value[ind] = true;
        isDown.value[ind] = false;
      } else {
        isUp.value[ind] = false;
        isDown.value[ind] = true;
      }
      for (let i = 0; i < isUp.value.length; i++) {
        if (i != ind) {
          isUp.value[i] = false;
          isDown.value[i] = false;
        }
      }
      options.value = options.value.sort(function (a: any, b: any) {
        let a1 = a[String(props.fieldName[ind])];
        let b1 = b[String(props.fieldName[ind])];
        if (data == 0) {
          return a1 - b1;
        } else {
          return b1 - a1;
        }
      });
    }
    let TextData = ref();
    let fixedWidth = ref();
    onMounted(() => {
      fixedWidth.value = TextData.value.offsetWidth;
    });
    return {
      widthData,
      isUp,
      isDown,
      setSort,
      options,
      TextData,
      fixedWidth,
    };
  },
});

import { useCssVars as _useCssVars } from 'vue'
